<template>
  <div>
    <section>
      <user-profile>
        <router-link slot="username" :to="`/user/${items.user}`">{{ items.user }}</router-link>
        <div slot="time">
          {{ "Posted: " + items.time_ago }}
        </div>
        <div slot="karma">
          {{ items.karma }}
        </div>
      </user-profile>
      <h2>{{ items.title }}</h2>
    </section>
    <section>
      <div v-html="items.content"></div>
    </section>
  </div>
</template>

<script>
import UserProfile from "../components/userProfile.vue";
export default {
  components: {
    UserProfile,
  },
  computed: {
    items() {
      return this.$store.state.items;
    },
  },
  created() {
    const id = this.$route.params.id;
    this.$store.dispatch("FETCH_ITEM", id);
  }
}
</script>

