import Vue from "vue"
import Vuex from "vuex";
import { fetchNewsList, fetchAskList, fetchJobsList, fetchUserInfo, fetchItemInfo, fetchList } from "../api/api.js"

Vue.use(Vuex);

const store = new Vuex.Store({
  state: {
    news: [],
    ask: [],
    jobs: [],
    user: [],
    items: [],
    list: [],
  },
  getters: {
    fetchedNews(state) {
      return state.news;
    },
    fetchedAsk(state) {
      return state.ask;
    },
    fetchedJobs(state) {
      return state.jobs;
    },
    fetchedUser(state) {
      return state.user;
    },
  },
  mutations: {
    SET_NEWS(state, data) {
      state.news = data;
    },
    SET_ASK(state, data) {
      state.ask = data;
    },
    SET_JOBS(state, data) {
      state.jobs = data;
    },
    SET_USER(state, data) {
      state.user = data;
    },
    SET_ITEM(state, data) {
      state.items = data;
    },
    SET_LIST(state, data) {
      state.list = data;
    }
  },
  actions: {
    FETCH_NEWS(context) {
      return fetchNewsList()
        .then(response => {
          context.commit("SET_NEWS", response.data);
          return response;
        })
        .catch(e => console.log(e));
    },
    FETCH_ASK(context) {
      return fetchAskList()
        .then(response => {
          context.commit("SET_ASK", response.data);
        })
        .catch(e => console.log(e));
    },
    FETCH_JOBS({ commit }) {
      return fetchJobsList()
        .then(({ data }) => {
          commit("SET_JOBS", data);
        })
        .catch(e => console.log(e));
    },
    FETCH_USER(context, name) {
      return fetchUserInfo(name)
        .then(response => {
          context.commit("SET_USER", response.data);
        })
        .catch(e => console.log(e));
    },
    FETCH_ITEM({ commit }, id) {
      return fetchItemInfo(id)
        .then(({ data }) => {
          commit("SET_ITEM", data);
        })
        .catch(e => console.log(e));
    },
    FETCH_LIST(context, pagename) {
      return fetchList(pagename)
        .then(response => {
          context.commit("SET_LIST", response.data);
        })
        .catch(e => console.log(e));
    }
  }
})


export default store;
