<template>
  <div>
    <tool-bar></tool-bar>
      <router-view></router-view>
    <transition name="page">
    </transition>
    <spinner :loading="loadingStatus"></spinner>
  </div>
</template>

<script>
import ToolBar from "./components/ToolBar.vue";
import Spinner from "./components/Spinner.vue";
import EventBus from "./utils/bus.js";
export default {
  data() {
    return {
      loadingStatus: false,
    }
  },
  components: {
    ToolBar,
    Spinner,
  },
  methods: {
    startSpinner() {
      this.loadingStatus = true;
    },
    endSpinner() {
      this.loadingStatus = false;
    }
  },
  created() {
    EventBus.$on("start:spinner", this.startSpinner);
    EventBus.$on("end:spinner", this.endSpinner);
  },
  beforeDestroy() {
    EventBus.$off("start:spinner", this.startSpinner);
    EventBus.$off("end:spinner", this.endSpinner);
  }
}
</script>

<style>
  *{margin: 0; padding: 0;}
  .list{padding: 4px 0;}
  a{text-decoration: none; color: #34495e;}
  a:hover{color: #42b83b;}
  a.router-link-exact-active{text-decoration: underline;}

  /* 
    router transition 
    <transition> 태그의 name vaule가 -enter-active, -leave-active 앞에 프리픽스 됨
  */
  .page-enter-active, .page-leave-active {
    transition: opacity .5s;
  }
  .page-enter, .page-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
  }
</style>
